/* ==========================================================================
   WooCommerce Buttons styles - begin
   ========================================================================== */

.woocommerce-page .edgtf-content,
div.woocommerce {
    
    a.button,
    a.added_to_cart,
    input[type="submit"],
    button[type="submit"]:not(.edgtf-search-submit):not(.price_slider_amount .button),
    .wc-forward:not(.added_to_cart):not(.checkout-button) {
        @include edgtfButtonDefaultStyle();
        @include edgtfButtonSize();
        @include edgtfButtonSolidColor();
        white-space: nowrap;
        cursor: pointer;
        z-index: 3;

        &:hover {
            color: $second-main-color;
	        @include edgtfShadow();
        }

    }

    *:not(.price_slider_amount) > button[type="submit"]:not(.edgtf-search-submit):hover {
        color: $second-main-color;
        @include edgtfShadow();
    }
    
    a.button.added {
        display: none;
    }
}
/* ==========================================================================
   WooCommerce Buttons styles - end
   ========================================================================== */