/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - begin
   ========================================================================== */

@mixin edgtfWooCommerceMarksGeneralStyle() {
    position: absolute;
    display: block;
    top: 0;
    width: auto;
	@include edgtfDefaultHeadingStyle;
    margin: 0;
    padding: 4px 16px;
    color: $second-main-color;
    font-size: 12px;
    line-height: 24px;
    letter-spacing: 1px;
    text-align: center;
    z-index: 100;
    box-sizing: border-box;
    @include edgtfShadow;
    @include edgtfTransform(translateZ(0));
}

@mixin edgtfWooCommerceSaleStyle() {
    left: 0;
    background-color: #fff;
}

@mixin edgtfWooCommerceOutOfStockStyle() {
    left: 0;
    background-color: #fff;
}

@mixin edgtfWooCommerceNewProductStyle() {
    left: 0;
    background-color: #fff;
}

/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Price mixin styles - begin
   ========================================================================== */

@mixin edgtfWooCommercePriceStyle($location: null) {
	
	@if($location == single) {
		position: relative;
		@include edgtfH4;
	} @else {
		@include edgtfH6;
	}
    top: 0;
    right: 0;
    display: block;
    @include edgtfDefaultHeadingStyle;
    
	margin: 0;
    color: $default-heading-color;
    
    * {
        font-size: inherit;
        line-height: inherit;
    }
    
    del {
        margin: 0 8px 0 0;
        font-weight: 400;
        color: rgba($default-heading-color, .5);
    }
    
    ins {
        text-decoration: none;
    }
}
/* ==========================================================================
   Price mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Star Ratings mixin styles - begin
   ========================================================================== */

%star-ratings {
    font-family: "ElegantIcons";
    font-size: 12px;
    line-height: 1;
    letter-spacing: 2px;
    color: $default-text-color;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@mixin edgtfWooCommerceRatingsStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    @extend %star-ratings;
    overflow: hidden;
    
    &:before {
        content: "\e031\e031\e031\e031\e031";
    }
    
    span {
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;
        
        &:before {
            content: "\e033\e033\e033\e033\e033";
        }
    }
}

@mixin edgtfWooCommerceSingleProductRatingsStyle() {
    
    &:before {
        content: "\e031";
        position: relative;
        display: inline-block;
        vertical-align: middle;
        @extend %star-ratings;
        overflow: hidden;
    }
    
    &.active:after {
        content: "\e033\e033\e033\e033\e033";
        position: absolute;
        right: 0;
        bottom: 7px;
        display: block;
        @extend %star-ratings;
    }
}
/* ==========================================================================
   Star Ratings mixin styles - end
   ========================================================================== */