/* ==========================================================================
   WooCommerce Widgets styles - begin
   ========================================================================== */

.woocommerce-page.archive aside.edgtf-sidebar {
    margin-top: 80px;

    @include ipad-landscape {
        margin-top: 23px;
    }

}

.widget.woocommerce {
    $thumb: 78px;
    $inner-padding: 15px; // distance between image and content
    $widget-list-item-padding: 15px; // same as sidebar vars

    clear: both;

    li {
        position: relative;
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // title

    .product-title {
	    @include edgtfDefaultHeadingStyle();
        @include edgtfH6();
	    margin: 15px 0 5px;
        
        &:hover {
            @include edgtfFontShadowHover()
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // star rating

    li:not(.wc-layered-nav-rating) {
        .star-rating {
            margin-bottom: 7px;
            margin-top: 7px;

            span {
                max-width: 100px;
            }
        }
    }

    li.wc-layered-nav-rating {
        a {
            text-decoration: none;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // price

    .amount {
        color: inherit;
	    @include edgtfDefaultHeadingStyle();
	    @include edgtfH6();

        span { // currency symbol
            color: inherit;
        }
    }

    del,
    ins {
        display: inline-block;
	    @include edgtfDefaultHeadingStyle();
	    @include edgtfH6();

        // currency symbol
        span {
            color: inherit;
        }
    }

    ins {
        text-decoration: none;
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // layered nav

    &.widget_layered_nav {
        .chosen {
            a {
                color: $first-main-color;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // price filter

    &.widget_price_filter {
        form {
            margin-top: 39px;
        }

        .price_slider_wrapper {
            .ui-widget-content {
                background-color: #fff;
                height: 4px;
                position: relative;

                .ui-slider-handle {
                    background-color: $second-main-color;
                    cursor: pointer;
                    height: 15px;
                    margin: 0 0 0 -1px;
                    outline: medium none;
                    position: absolute;
                    top: -6px;
                    width: 7px;
                    z-index: 2;

                    &:last-child {
                        @include edgtfTransform(translateX(-50%));
                    }
                }

                .ui-slider-range {
                    background-color: $second-main-color;
                    display: block;
                    height: 4px;
                    position: absolute;
                    top: 0;
                    z-index: 1;
                }
            }
        }

        .price_slider_amount {
            margin: 10px 0 0;

            .button {
                float: left;
                padding: 8px 20px;
                margin-left: -20px;

                @include edgtfButtonTransparentColor();

                font-size: 17px;
                letter-spacing: 0;

                &:hover {
                    @include edgtfFontShadowHuge();
                }
            }

            .price_label {
                float: right;
                line-height: 50px; // to match button height
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with images

    &.widget_shopping_cart,
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products {
        ul {
            li {
                min-height: 88px;
                position: relative;

                a {
                    img {
                        left: 0;
                        max-width: $thumb;
                        position: absolute;
                        top: $widget-list-item-padding;
                        width: 100%;
                    }

                    span {
                        box-sizing: border-box;
                        display: block;
                        padding: 0 0 0 #{$thumb + $inner-padding};
                        position: relative;
                    }
                }

                > *:not(a) {
                    margin: 0 0 0 #{$thumb + $inner-padding};
                }
	            
	            ins {
		            margin-left: .5em;
	            }
	            
		        .amount:not(:first-of-type) {
			        margin-left: initial;
	            }
	            
	            del span {opacity: .8}
            }
        }
    }

    &.widget_top_rated_products {
        .star-rating {
            display: none;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with images and plain text next to image

    &.widget_shopping_cart,
    &.widget_recent_reviews {
        a {
            display: block;
            padding: 0 0 0 #{$thumb + $inner-padding};

            @include edgtfH6();
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // recent reviews

    //&.widget_recent_reviews {
    //    .reviewer {
    //    }
    //}

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // shopping cart

    &.widget_shopping_cart {
        .remove {
            font-size: 0;
            font-weight: normal;
            line-height: 1;
            position: absolute;
            right: 0;
            top: 5px;
            text-decoration: none;

            &:after {
                content: "\4d";
                display: block;
                font-family: 'ElegantIcons';
                font-size: 15px;
                line-height: 15px;
                position: relative;
            }
        }

        .variation {
            @include edgtfRelativeHolderLayout();

            > * {
                display: inline-block;
                margin: 0;
                vertical-align: middle;

                p {
                    margin: 0;
                }
            }
        }

        .total {
            margin: 17px 0 13px;
        }

        .buttons {
            .button {
                @include edgtfButtonDefaultStyle();
                @include edgtfButtonSize();
                @include edgtfButtonSolidColor();

                &:hover {
                    @include edgtfButtonSolidHoverColor();
                }

                &:first-child {
                    float: left;
                }

                &:last-child {
                    float: right;
                }
            }
        }
    }
}

/* ==========================================================================
   WooCommerce Widgets styles - end
   ========================================================================== */