/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.edgtf-woo-single-page {

    .edgtf-content .edgtf-content-inner > .edgtf-container > .edgtf-container-inner,
    .edgtf-content .edgtf-content-inner > .edgtf-full-width > .edgtf-full-width-inner {
        padding-top: 174px;
    }

    .edgtf-content .woocommerce-message:first-child {
        margin-top: 30px;
    }
	
	&.edgtf-woo-single-thumb-on-left-side {
		
		.edgtf-single-product-content {
			
			.images {
				
				@media only screen and (min-width: $ipad-portrait-plus-pixel) {
					
					figure {
						width: 100%;
						margin: 0;
					}
					
					$thumb_size: 180;
					$space_between_items: 27;
					
					.woocommerce-product-gallery__image {
						
						&:first-child {
							$first_thumb_offset: $thumb_size + $space_between_items + 1;
							
							left: #{$first_thumb_offset}px;
							width: calc(100% - #{$first_thumb_offset + 4}px);

                            @include laptop-landscape-mac() {
                                left: 156px;
                                width: calc(100% - 162px);
                            }

                            @include laptop-landscape-medium() {
                                left: 166px;
                                width: calc(100% - 172px);

                            }
						}
					}
					
					&.woocommerce-product-gallery--with-images {
						
						.woocommerce-product-gallery__image {
							
							&:not(:first-child) {
								position: absolute;
								top: 0;
								left: 0;
								width: #{$thumb_size}px;
								padding: 0 0 #{$space_between_items}px;
								box-sizing: border-box;
								
								a {
									height: 100%;

									img {
										height: 100%;
									}
								}

                                @include laptop-landscape-mac() {
                                    width: 130px;
                                }
                                @include laptop-landscape-medium() {
                                    width: 140px;
                                }
							}
						}
					}
					
					$thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
					@for $i from 0 to length($thumb_3_columns_label) {
						&.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
							$column_value: $i+2;
							$column_size: 100 / $column_value;
							$column_width: calc(#{100% / $column_value} + #{$space_between_items / $column_value}px);
							
							.woocommerce-product-gallery__image {
								
								&:not(:first-child) {
									height: $column_width;
								}
								
								$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
								@for $i from 0 to length($thumbnail_numbers) {
									$thumbnail_number_value: nth($thumbnail_numbers, $i+1);
									
									&:nth-child(#{$thumbnail_number_value}#{n}) {
										$first_param: ($column_size * $i)#{'%'};
										$second_param: ($space_between_items / $column_value * $i)#{px};
										$topPos: calc(#{$first_param} + #{$second_param});
										
										top: $topPos;
									}
								}
							}
						}
					}
				}
			}
		}
	}
    
    .product {
	    @include edgtfRelativeHolderLayout();
	    float: none;
	    margin: 0 0 0;
    }
        
    .edgtf-single-product-content {
        @include edgtfRelativeHolderLayout();
        clear: both;
        margin: 0 0 10px;
	
	    .images {
		    position: relative;
		    display: inline-block;
		    vertical-align: middle;
		    width: 61%;
		    margin: 0;
		    padding: 0 6px 0 0;
		    float: left;
		    box-sizing: border-box;
		    
		    figure {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0 -10px;
			
			    a {
				    @include edgtfRelativeHolderLayout();
				
				    img {
					    position: relative;
					    width: 100%;
					    display: block;
				    }
			    }
		    }
		
		    .woocommerce-product-gallery__trigger {
				position: absolute;
			    top: 10px;
			    right: 39px;
			    width: 30px;
			    height: 30px;
			    line-height: 30px;
			    text-align: center;
			    z-index: 10;
		    }
		    
		    .woocommerce-product-gallery__image {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0;
			    padding: 0 10px;
			    box-sizing: border-box;
			    
			    &:first-child {
				    left: 10px;
				    width: calc(100% - 20px);
				    padding: 0;
			    }
			
			    .zoomImg {
				    cursor: crosshair;
			    }
		    }
		    
		    &.woocommerce-product-gallery--with-images {
			
			    .woocommerce-product-gallery__image {
				
				    &:not(:first-child) {
					    float: left;
					    padding-top: 20px;
				    }
			    }
		    }
		
		    $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
		    @for $i from 0 to length($thumb_3_columns_label) {
			    &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
				    $column_width: 100%/($i+2);
				
				    .woocommerce-product-gallery__image {
					
					    &:not(:first-child) {
						    width: $column_width;
					    }
				    }
			    }
		    }
	    }
    }
	
    .edgtf-single-product-summary {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 39%;
        padding: 0 0 0 61px;
        float: left;
        box-sizing: border-box;
	
	    .edgtf-single-product-title {
		    display: block;
		    margin: 0 0 3px;
	    }
	
	    .price {
		    @include edgtfWooCommercePriceStyle(single);
	    }
	
	    .woocommerce-product-rating {
		    @include edgtfRelativeHolderLayout();
		    line-height: 1;
		    margin: 18px 0 3px;
	    }
	
	    .woocommerce-product-details__short-description {
		    @include edgtfRelativeHolderLayout();
            margin: 34px 0 0;
            padding-top: 36px;
		    font-size: 16px;
		
		    p {
			    margin: 0;
		    }

            &:before {
                content: '';
                background-color: $first-main-color;
                display: block;
                height: 7px;
                width: 143px;
                position: absolute;
                top: 0;
                left: 0;
            }
	    }
	
	    .product_meta {
		    margin: 46px 0 0;
            font-size: 16px;
		
		    > span {
			    display: block;
			    margin: 3px 0;
			
			    a {
				    color: $default-text-color;
				
				    &:hover {
					    color: #fff;
					    @include edgtfFontShadowHover()
				    }
			    }
		    }
	    }
	
	    .edgtf-woo-social-share-holder {
		    @include edgtfRelativeHolderLayout();
		    margin: 0;
		
		    > * {
			    display: table-cell;
			    vertical-align: top;
		    }
		
		    > span {
			    padding: 0 9px 0 0;
		    }
	    }
	
	    p.stock.out-of-stock,
	    p.stock.in-stock {
		    color: $default-heading-color;
		    font-weight: bold;
		    margin: 18px 0 0;
	    }
	
	    form.cart {
		    @include edgtfRelativeHolderLayout();
		    margin: 46px 0 0;

            button.single_add_to_cart_button {
                padding: 17px 50px;
            }
	    }
	
	    .edgtf-quantity-buttons {
		    margin: 0 20px 0 0;
	    }
    }
	
	.woocommerce-tabs {
		@include edgtfRelativeHolderLayout();
		margin: 30px 0 0;
		
		ul.tabs {
			@include edgtfRelativeHolderLayout();
			list-style: none;
			margin: 0;
			padding: 0;
			clear: both;
			z-index: 10;
			
			> li {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				
				a {
					position: relative;
					display: inline-block;
					vertical-align: top;
					text-transform: uppercase;
					font-weight: 700;
					letter-spacing: 0.14em;
					padding: 27px 40px;
				}
				
				&.active {
					a {
						&:after {
							content:'';
							position: absolute;
							width:100%;
							height: 6px;
							bottom: 0;
							left: 0;
							background: #fff;
							@include edgtfShadow();
							@include edgtfFontShadow();
						}
					}
				}
			}
		}
		
		.wc-tab {
			background:#000;
			padding: 70px 50px;
			-webkit-box-sizing: border-box;
			-moz-box-sizing: border-box;
			box-sizing: border-box;
		}
		
		.entry-content {
			@include edgtfRelativeHolderLayout();
			
			> h2 {
				display: none;
			}
			
			> p {
				margin: 18px 0 20px;
			}
		}
		
		table {
			margin: 18px 0 20px;
			
			tr, th {
				border: 0;
			}
			
			td, th {
				padding: 3px 0;
				vertical-align: middle;
				text-align: left;
				
				p {
					margin: 0;
				}
			}
			
			th {
				color: $default-heading-color;
				width: 1%;
				padding: 0 10px 0 0;
                font-weight: 400;
			}
			
			td {
				width: 99%;
			}
		}
		
		#reviews {
			@include edgtfRelativeHolderLayout();
			margin: 18px 0 20px;
			
			h2 {
				font-size: 30px;
				line-height: 36px;
				margin: 8px 0 24px;
			}
			
			ol.commentlist {
				list-style: none;
				
				li {
					margin: 0 0 15px;
					
					&:last-child {
						margin: 0;
					}
				}
				
				.comment_container {
					@include edgtfRelativeHolderLayout();
					
					img {
						float: left;
						width: 110px;
					}
				}
				
				.comment-text {
					padding: 0 0 0 130px;

                    .woocommerce-review__author {
                        font-size: 20px;
                        line-height: 1.4em;
                        color: $default-heading-color;
                        font-weight: 700;
                        text-transform: uppercase;
                    }

                    .description {
                        margin-top: 10px;
                    }
					
					p {
						margin: 0;
					}
				}
			}
			
			.comment-respond {
				margin-top: 25px;
				
				.comment-reply-title {
					margin: 0 0 10px;
					@include edgtfDefaultHeadingStyle();
					@include edgtfH6();
				}
				
				p {
					margin: 0;

                    &.form-submit {
                        margin-top: 15px;
                    }
				}
				
				.stars {
					overflow: hidden;
					
					a {
						position: relative;
						display: inline-block;
						color: transparent;
						font-size: 0;
						margin: 0;
						@include edgtfWooCommerceSingleProductRatingsStyle();
					}
				}
			}
		}
	}
	
	.related.products,
	.upsells.products {
		margin: 48px 0 0;

        > h5 {
            font-size: 30px;
            line-height: 1.2em;
        }

        ul {
            margin-top: 35px;
        }
		
		.product {
			
			.edgtf-pl-text-wrapper {
				margin-bottom: 0;
			}
		}
	}
}
/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */