/* ==========================================================================
   Product List shortcode styles - begin
   ========================================================================== */

.edgtf-pl-holder {
    @include edgtfRelativeHolderLayout();
	
	&.edgtf-info-on-image {
		
		.edgtf-pli {
			
			.edgtf-pli-add-to-cart {
				margin: 25px 0 0;
			}
		}
	}

    .edgtf-pli {
        padding: 0;
        z-index: 100;

	    .edgtf-pli-title {
		    margin: 0 0 1px;
	    }
	
	    .edgtf-pli-category {
		    margin: 0;
	    }

        .edgtf-pli-excerpt {
            color: $default-heading-color;
	        line-height: 1.3em;
            margin: 11px 0 3px;
        }

	    .edgtf-pli-rating-holder {
		    @include edgtfRelativeHolderLayout();
		    line-height: 1;
		    margin: 10px 0 3px;
	    }

	    .edgtf-pli-rating {
		    @include edgtfWooCommerceRatingsStyle();
	    }

        .edgtf-pli-price {
	        @include edgtfWooCommercePriceStyle();
        }
    }

    &.edgtf-info-below-image {
        .edgtf-pli {
            .edgtf-pli-title {
                float: left;
                width: 60%;
            }
            .edgtf-pli-price {
                float: right;
                width: 40%;
                text-align: right;
            }
            .edgtf-pli-category {
                clear: both;
	            
	            a:hover {
			        color: #fff;
			        @include edgtfFontShadowHover();
		        }
            }
        }
    }

    .edgtf-pli-inner {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        overflow: hidden;

        &:hover {

            .edgtf-pli-text {
                opacity: 1;
            }
        }

        .touch & {
            cursor: pointer;
        }

        .edgtf-pli-image {
            @include edgtfRelativeHolderLayout();

            img {
                display: block;
                width: 100%;
            }

            .edgtf-pli-onsale,
            .edgtf-pli-out-of-stock,
            .edgtf-pli-new-product {
	            @include edgtfWooCommerceMarksGeneralStyle();
            }

            .edgtf-pli-onsale {
	            @include edgtfWooCommerceSaleStyle();
            }

            .edgtf-pli-out-of-stock {
	            @include edgtfWooCommerceOutOfStockStyle();
            }
	
	        .edgtf-pli-new-product {
		        @include edgtfWooCommerceNewProductStyle();
	        }
        }
            
        .edgtf-pli-text {
            @include edgtfAbsoluteHolderLayout();
            padding: 10px;
            opacity: 0;
            background-color: rgba(#000, 0.8);
            box-sizing: border-box;
            @include edgtfTransition(opacity .2s ease-out);
        }

        .edgtf-pli-text-outer {
	        @include edgtfTableLayout();
	        text-align: center;
        }

        .edgtf-pli-text-inner {
            @include edgtfTableCellLayout();

            .edgtf-pli-title,
            .edgtf-pli-category a,
            .edgtf-pli-excerpt,
            .edgtf-pli-rating,
            .edgtf-pli-price {
                color: #fff;
            }
	
	        .edgtf-pli-category a:hover {
		        color: #fff;
		        @include edgtfFontShadowHover();
	        }

	        .edgtf-pli-add-to-cart {

		        &.edgtf-default-skin {

			        .button,
			        .added_to_cart {
				        @include edgtfButtonSolidColor();

				        &:hover {
					        @include edgtfShadow();
				        }
			        }
		        }

		        &.edgtf-light-skin {

			        .button,
			        .added_to_cart {
				        color: $default-heading-color;
				        background-color: #fff;

				        &:hover {
					        color: #fff;
					        background-color: $first-main-color;
				        }
			        }
		        }
		
		        &.edgtf-dark-skin {
			
			        .button,
			        .added_to_cart {
				        color: #fff;
				        background-color: $default-heading-color;
				
				        &:hover {
					        color: #fff;
					        background-color: $first-main-color;
				        }
			        }
		        }
	        }

            .button,
            .added_to_cart {
	            @include edgtfButtonDefaultStyle();
	            @include edgtfButtonSize(small);
	            white-space: nowrap;
	            z-index: 3;
            }

            .button.added {
                display: none;
            }
        }

	    .edgtf-pli-link {
		    @include edgtfAbsoluteHolderLayout();
	    }
    }

    .edgtf-pli-text-wrapper {
        @include edgtfRelativeHolderLayout();
        margin: 22px 0 0;
    }
	
	&.edgtf-product-info-light {
		
		.edgtf-pli-inner {
			
			.edgtf-pli-text-inner {
				
				.edgtf-pli-title,
				.edgtf-pli-category a,
				.edgtf-pli-excerpt,
				.edgtf-pli-rating,
				.edgtf-pli-price {
					color: #fff;
				}
				
				.edgtf-pli-category a:hover {
					color: #fff;
					@include edgtfFontShadowHover();
				}
			}
		}
	}
	
	&.edgtf-product-info-dark {
		
		.edgtf-pli-inner {
			
			.edgtf-pli-text-inner {
				
				.edgtf-pli-title,
				.edgtf-pli-category a,
				.edgtf-pli-excerpt,
				.edgtf-pli-rating,
				.edgtf-pli-price {
					color: $default-heading-color;
				}
				
				.edgtf-pli-category a:hover {
					color: #fff;
					@include edgtfFontShadowHover();
				}
			}
		}
	}

	/*************** Masonry Layout - begin ***************/
	
	&.edgtf-masonry-layout {
		
		.edgtf-pli-inner {
			width: 100%;
			height: 100%;
			
			.edgtf-pli-image {
				height: 100%;
			}
		}
	}
	
	/*************** Masonry Layout - end ***************/
}
/* ==========================================================================
   Product List shortcode styles - end
   ========================================================================== */