/* ==========================================================================
   WooCommerce Cart page styles - begin
   ========================================================================== */

.edgtf-woocommerce-page {

    .blockOverlay {
        background-color: $default-background-color !important;
    }
	
	table.cart {

        margin-top: 33px;
		
		thead {
			
			tr {
				border: 0;
				border-bottom: 1px solid $default-border-color;
				
				th {
					padding: 10px 0;
					color: $default-heading-color;
					text-align: left;
				}
			}
		}
		
		tbody {
			
			tr {
				border: 0;
			}
		}
		
		tr.cart_item {
			border-bottom: 1px solid $default-border-color;
			
			td {
				padding: 25px 0;
				vertical-align: middle;
				text-align: inherit;
				
				&.product-remove {
					padding-left: 10px;
					padding-right: 10px;
					padding-bottom: 33px;

					a {
						color: $default-text-color;
						font-size: 0;
						line-height: 1;
						
						&:after {
							content: "\f404";
							display: inline-block;
							vertical-align: middle;
							speak: none;
							color: inherit;
							font-family: "Ionicons";
							font-size: 25px;
							line-height: inherit;
							font-style: normal;
							font-weight: normal;
							font-variant: normal;
							text-transform: none;
							text-rendering: auto;
							-webkit-font-smoothing: antialiased;
							-moz-osx-font-smoothing: grayscale;
						}
					}
				}
				
				a:hover {
					color: #fff;
					@include edgtfFontShadowHover();
				}
				
				&.product-thumbnail {
					width: 86px;
					padding-right: 10px;
					
					a, img {
						display: block;
					}
				}
				
				.variation {
					
					> * {
						display: inline-block;
						vertical-align: middle;
						padding: 0;
						margin: 0;
					}
					
					p {
						margin: 0;
					}
				}
			}
		}
		
		td.actions {
			padding: 35px 0 36px;
			text-align: inherit;
			
			.coupon {
				
				position: relative;
				display: inline-block;
				vertical-align: top;
				
				input[type=text] {
					$input-height: 68px;
					height: 68px;
					line-height: calc(68px - (#{$input-vertical-padding} * 2) - 4px);
				}
				
				label {
					display: none;
				}
			}

			.button {
				margin: 0 0 0 22px;
				float:right;
			}
		}
	}
	
	.cart-collaterals {
		@include edgtfRelativeHolderLayout;
		margin: 0 0 40px;
		
		h2 {
			position: relative;
			display: block;
			margin: 0 0 26px;
		}
		
		table {
			margin: 0 0 35px;
			
			tr {
				border: 0;
				border-bottom: 1px solid $default-border-color;
			}
			
			td, th {
				padding: 14px 0;
				text-align: left;
				vertical-align: middle;
			}
			
			th {
				width: 20%;
				padding-right: 5px;
				border: 0;
			}
		}
		
		tr.shipping {
			
			#shipping_method {
				list-style: none;
				margin: 0 0 7px;
				
				li {
					margin: 3px 0;
					padding: 0;
					
					> * {
						display: inline-block;
						vertical-align: middle;
						margin: 0;
					}
				}
			}
		}
		
		.woocommerce-shipping-calculator {
			
			.shipping-calculator-button {
				display: block;
				margin: 0 0 10px;
			}
			
			p {
				margin: 0;
			}
		}
	}
	
	.cross-sells {
		margin: 30px 0 0;
		border-top: 1px solid $default-border-color;
		
		> h2 {
			display: block;
			margin: 36px 0 50px;
		}
	}
	
	.cart_totals {
		margin: 1px 0 20px;
	}
}

.edgtf-woocommerce-page {
	
	.cart-empty {
		position: relative;
		display: block;
		text-align: center;
		margin: 40px 0 33px;

        font-size: 50px;
        line-height: 1.12em;
        color: $default-heading-color;
        font-weight: 700;
        text-transform: uppercase;
	}
	
	.return-to-shop {
		position: relative;
		display: block;
		text-align: center;
		margin: 0 0 60px;
	}
}
/* ==========================================================================
   WooCommerce Cart page styles - end
   ========================================================================== */