/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.edgtf-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    margin: 0 15px;

    .touch & {
        cursor: pointer;
    }

    .edgtf-shopping-cart-inner {
        position: relative;
        display: table;
        height: 100%;
    }

    .edgtf-header-cart {
        position: relative;
        display: table-cell;
        vertical-align: middle;
        line-height: 1;

        .edgtf-cart-icon {
            position: relative;
            display: inline-block;
            vertical-align: top;

            .edgtf-cart-number {
                position: absolute;
                top: -4px;
                right: -4px;
                display: block;
                background-color: $second-main-color;
                height: 16px;
                width: 16px;
                line-height: 16px !important; /* overwrite inherit */
                text-align: center;
                font-size: 11px;
                color: #fff;
                border-radius: 50%;

            }
        }

        &.edgtf-header-cart-icon-pack {
            
            .edgtf-cart-icon {
                font-size: 27px;
                line-height: 1em;
    
                * {
                    display: block;
                    line-height: inherit;
        
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }

        &.edgtf-header-cart-svg-path {
            
            .edgtf-cart-icon {

                > * {
                    vertical-align: middle;
                }
                
                > *, 
                path {
                    fill: currentColor;
                    stroke: currentColor;
                }
            }
        }
    }

    &:hover {
		.edgtf-header-cart-icon-pack {
            color: #fff;
			filter: drop-shadow(-2px 0 7px rgba(255, 255, 255, .7)) drop-shadow(1px 1px 7px rgba(255, 255, 255, .7));
			-webkit-filter: drop-shadow(-2px 0 7px rgba(255, 255, 255, .7)) drop-shadow(1px 1px 7px rgba(255, 255, 255, .7));
	    }

        .edgtf-shopping-cart-dropdown {
            top: 100%;
            opacity: 1;
            visibility: visible;
        }
    }
}

.edgtf-dark-header .edgtf-page-header > div:not(.edgtf-sticky-header):not(.fixed) .edgtf-shopping-cart-holder .edgtf-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $header-dark-hover-color;
    }
}

.edgtf-light-header .edgtf-page-header > div:not(.edgtf-sticky-header):not(.fixed) .edgtf-shopping-cart-holder .edgtf-header-cart {
    color: $header-light-color;

    &:hover {
        color: $header-light-hover-color;
    }
}

.edgtf-shopping-cart-dropdown {
    position: absolute;
    top: calc(100% + 50px);
    left: auto;
    right: 0;
    width: 330px;
    background-color: #1b0e0a;
    background-size: cover;
    text-align: left;
    z-index: 1000;
    opacity: 0;
    visibility: hidden;
    box-sizing: border-box;
    @include edgtfTransition(all 0.35s cubic-bezier(.53,.01,.18,1));

    ul {
        margin: 48px 48px 34px;
        padding: 0;

        li {
            @include edgtfTableLayout();
            padding: 6px 0;
            box-sizing: border-box;

            &.edgtf-empty-cart {
                margin-bottom: 52px;
            }

            &:first-child {
                padding-top: 0;
            }

            &:last-child {
                padding-bottom: 0;
            }
        }
    }

    .edgtf-item-image-holder {
        position: relative;
        display: table-cell;
        height: 100%;
        width: 77px;
        vertical-align: top;

        img {
            display: block;
        }
    }

    .edgtf-item-info-holder {
        @include edgtfTableCellLayout();
        padding-left: 15px;

        .edgtf-product-title {
            display: block;
            margin: 0;
            padding: 0 20px 0 0;
            box-sizing: border-box;
        }

        .remove {
            position: absolute;
            top: -1px;
            right: -5px;
	        font-size: 22px;
	        line-height: 1;

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .edgtf-quantity {
            margin: 3px 0 0;
            font-size: 14px;
        }

        .amount {
            margin: 3px 0 0;
            font-size: 14px;
            color: $default-heading-color;
        }
    }

    .edgtf-cart-bottom {
        padding: 15px 0 0;

        .edgtf-subtotal-holder {
            position: relative;
            display: table;
            width: 100%;
            margin: 0 0 24px;
            text-transform: uppercase;

            > * {
                color: $default-heading-color;
                line-height: 20px;
                font-size: 17px;
                font-weight: 700;
            }
        }

        .edgtf-view-cart,
        .edgtf-view-checkout {
            @include edgtfRelativeHolderLayout();
            width: auto;
            height: 44px;
            line-height: 44px;
            padding: 0;
            font-size: 17px;
            font-weight: 700;
	        letter-spacing: 0.014em;
            color: #fff;
            background-color: transparent;
            text-align: center;
            box-sizing: border-box;
            @include edgtfTransition(color .2s ease-out);

            span {
                display: block;
            }

            &:hover {
                color: #fff;
	            @include edgtfFontShadowHover()
            }
        }

        .edgtf-view-cart {
            float: left;
            margin-right: 5px;
        }

        .edgtf-view-checkout {
            float: right;
            margin-left: 5px;
        }
    }
}

.edgtf-vertical-menu-area {

    .edgtf-shopping-cart-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */